<template>
  <div>
    <el-dialog title="登录" :visible.sync="loginVisible" :show-close="false" :close-on-click-modal="false"
               :close-on-press-escape="false">
      <el-form :model="loginForm" label-width="80px">
        <el-form-item>
        </el-form-item>
        <el-form-item label="手机号">
          <el-input v-model="loginForm.username"/>
        </el-form-item>
        <el-form-item label="密码">
          <el-input v-model="loginForm.password" show-password/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="login">登录</el-button>
      </div>
    </el-dialog>
    <el-button
        v-for="item in liveCameras"
        :key="item.id"
        type="primary"
        @click="handlePlay(item)"
        style="margin-bottom: 20px">
      {{ item.name }}
    </el-button>
    <easy-player
        id="player"
        :video-url="videoUrl"
        :video-title="videoTitle"
        muted
        style="width: 100%; height: 768px"
        ref="easyPlayer"
    />
  </div>
</template>

<script>
import axios from "axios";
import EasyPlayer from "@easydarwin/easyplayer";

export default {
  components: {EasyPlayer},
  data() {
    return {
      token: null,
      loginVisible: true,
      loginForm: {
        username: null,
        password: null
      },
      liveCameras: [],
      videoUrl: null,
      videoTitle: null
    }
  },
  created() {
    const token = localStorage.getItem("token");
    if (token) {
      this.token = token;
      this.loginVisible = false;
      this.getMyLive();
    }
  },
  methods: {
    login() {
      if (!this.loginForm.username) {
        this.$message.error("请输入手机号");
        return false;
      }
      if (!this.loginForm.password) {
        this.$message.error("请输入密码");
        return false;
      }
      this.request("/api/Account/Login", {
        strUser: this.loginForm.username,
        strPwd: this.$md5(this.loginForm.password)
      }, data => {
        this.token = data.Ticket;
        localStorage.setItem("token", this.token);
        this.loginVisible = false;
        this.getMyLive();
      });
    },
    getMyLive() {
      this.liveCameras = [];
      this.request("/api/Live/GetMyLive", {
        classId: "19fe09141cd6443ea2eca989e680289d"
      }, data => {
        data.LiveCameras.forEach(item => {
          this.liveCameras.push({
            id: item.CameraId,
            name: item.Name
          });
        });
      })
    },
    handlePlay(item) {
      this.request("/api/Live/GetLiveAuth2", {
        cameraId: item.id,
        vType: 1,
        quality: 0
      }, data => {
        this.videoUrl = data.AuthUrl;
        this.videoTitle = item.name;
      });
    },
    request(url, params, accept) {
      const loading = this.$loading({
        lock: true,
        text: '玩命加载中',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      axios.get(url, {
        headers: {
          auth: this.token
        },
        params: params
      }).then(resp => {
        const data = resp.data;
        if (data.code === 200) {
          accept(resp.data.data);
        } else if (data.code === 10011) {
          this.token = null;
          localStorage.removeItem("token");
          this.loginVisible = true;
          this.$message.error("会话过期，请重新登录");
        } else {
          this.$message.error(data.msg);
        }
        loading.close();
      }).catch(() => {
        this.$message.error("请求失败");
        loading.close();
      })
    },
  }
}
</script>

